html {
  scroll-behavior: smooth;
}
.header{
    background: url(../img/background.jpg);
    height: 655px;
    background-size: cover;
    position: relative;
    .overlay{
        background-color: #00000063;
        position: absolute;
        width: 100%;
        height: 655px;
        top:0;
        left: 0;
    }
    
    //navbar
    .navbar{
        .fab{
            color: white;
            font-size: 30px;
        }
        
    .nav-link{
        color: white;
        font-family: 'Heebo', sans-serif;
         font-size: 18px;

    }
 }

    //content
.content{
    position: relative;
    top: -75%; 
    h1{
    font-family: 'Anton', sans-serif;
    color: white;
    font-size: 75px;
    @media (max-width: 575.98px) {font-size: 60px;}

    }
    h3{
    color: white;
    font-family: 'Merienda', cursive;

    }
    .resume{
    border: 2px solid white;
    padding: 10px 20px;
    text-decoration: none;
    margin-top: 30px;
    display: inline-block;
    font-size: 20px;
    color: white;
    font-family: 'Anton', sans-serif;
    }
    
    .next{
        color: white;
        display: block;
        margin-top: 15%;
    text-decoration: none;
    }
}
}

